.clearfix:before,.clearfix:after{content:" ";display:table}.clearfix:after{clear:both}.clearfix{*zoom:1}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}html{height:100%}body{padding:0;height:100%;font-family:"HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;font-weight:300}#app{color:#2c3e50;height:100%;width:100%;font-family:Source Sans Pro, Helvetica, sans-serif;text-align:center}#app a{color:#42b983;text-decoration:none}#app #sidebar,#app #main{padding:1em;float:left;width:50%;height:100%}#app #sidebar>.textarea{display:block;width:100%;height:100%;overflow-y:scroll;text-align:justify;line-height:1.5;background-color:#f5f5f5;padding:1em;border:0}#app #main>textarea{width:100%}#app #result{max-height:300px;overflow-y:scroll;text-align:left}#app #download a{display:inline-block;background-color:#42b983;color:#fff !important;padding:.5em 1em;border-radius:4px}



